import apiClient from 'services/axios'
import store from 'store'

export async function login(email, password, rememberMe) {
  return apiClient
    .post('/kctauth/login', {
      username: email,
      password,
    })
    .then(response => {
      if (response) {
        const { token: accessToken, user } = (response.data || {}).data || {}
        if (accessToken) {
          store.set('accessToken', accessToken)
          store.set('user', rememberMe ? { ...user, authorized: true } : {})
        }
        return response.data
      }
      return false
    })
    .catch(err => console.log(err))
}

export async function register(email, password, name) {
  return apiClient
    .post('/kctauth/register', {
      email,
      password,
      name,
    })
    .then(response => {
      if (response) {
        const { accessToken } = response.data
        if (accessToken) {
          store.set('accessToken', accessToken)
        }
        return response.data
      }
      return false
    })
    .catch(err => console.log(err))
}

export async function logout() {
  return apiClient
    .get('/kctauth/logout')
    .then(() => {
      store.remove('accessToken')
      store.remove('user')
      return true
    })
    .catch(err => console.log(err))
}

export async function getHelpdesk(queryParams) {
  const params = Object.entries(queryParams).reduce((acc, val) => {
    const [key, value] = val
    acc[key] = JSON.stringify(value)
    return acc
  }, {})
  return apiClient
    .get('/helpdesk', { params })
    .then(response => {
      if (response) {
        return response.data
      }
      return false
    })
    .catch(err => console.log(err))
}

