import { all, takeEvery, put, call, select } from 'redux-saga/effects'
import { notification } from 'antd'
import * as jwt from 'services/jwt'
import actions from './actions'

const mapAuthProviders = {
  jwt: {
    getWorkspace: jwt.getWorkspace,
  },
}

export function* LOAD_WORKSPACE() {
  yield put({
    type: 'workspace/SET_STATE',
    payload: {
      loading: true,
    },
  })
  const { authProvider: autProviderName } = yield select(state => state.settings)
  const { searchConfig } = yield select(state => state.workspace)
  const response = yield call(mapAuthProviders[autProviderName].getWorkspace, searchConfig)
  const { success, data = [], message, total, limit } = response || {}
  const list = data.map(item => ({
    ...item,
    key: item.guid,
  }))
  if (success) {
    yield put({
      type: 'workspace/SET_STATE',
      payload: {
        list,
        total,
        limit,
      },
    })
  }
  if (!success) {
    notification.error({
      message,
      description: 'Failed retrieving help desk',
    })
  }
  yield put({
    type: 'workspace/SET_STATE',
    payload: {
      loading: false,
    },
  })
}

export function* SEARCH_WORKSPACE() {
  yield put({
    type: 'workspace/LOAD_WORKSPACE',
  })
}

export default function* rootSaga() {
  yield all([
    takeEvery(actions.LOAD_WORKSPACE, LOAD_WORKSPACE),
    takeEvery(actions.SEARCH_WORKSPACE, SEARCH_WORKSPACE),
  ])
}
