import { all, takeEvery, put, call, select } from 'redux-saga/effects'
import { notification } from 'antd'
import * as jwt from 'services/jwt'
import actions from './actions'

const mapAuthProviders = {
  jwt: {
    getHelpdesk: jwt.getHelpdesk,
  },
}

export function* LOAD_HELPDESK() {
  yield put({
    type: 'helpdesk/SET_STATE',
    payload: {
      loading: true,
    },
  })
  const { authProvider: autProviderName } = yield select(state => state.settings)
  const { searchConfig } = yield select(state => state.helpdesk)
  const response = yield call(mapAuthProviders[autProviderName].getHelpdesk, searchConfig)
  const { success, data, message, total, limit } = response || {}
  const list = data.map(item => ({
    ...item,
    key: item.guid,
  }))
  if (success) {
    yield put({
      type: 'helpdesk/SET_STATE',
      payload: {
        list,
        total,
        limit,
      }
    })
  }
  if (!success) {
    notification.error({
      message,
      description: 'Failed retrieving help desk',
    })
  }
  yield put({
    type: 'helpdesk/SET_STATE',
    payload: {
      loading: false,
    },
  })
}

export function* SEARCH_HELPDESK() {
  yield put({
    type: 'helpdesk/LOAD_HELPDESK',
  })
}

export default function* rootSaga() {
  yield all([
    takeEvery(actions.LOAD_HELPDESK, LOAD_HELPDESK),
    takeEvery(actions.SEARCH_HELPDESK, SEARCH_HELPDESK)
  ])
}
