import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import {
  // Menu,
  Alert,
  Button,
} from 'antd'
import { SoundOutlined, MenuOutlined } from '@ant-design/icons'
import {
  // Link,
  withRouter,
} from 'react-router-dom'
import classNames from 'classnames'
import store from 'store'
import { find } from 'lodash'
import style from './style.module.scss'
// import LanguageSwitcher from '../LanguageSwitcher';
import Actions from '../Actions'
import UserMenu from '../UserMenu'
// import SubMenu from '../SubMenu';

const mapStateToProps = ({ menu, settings, user }) => ({
  menuData: menu.menuData,
  logo: settings.logo,
  menuColor: settings.menuColor,
  role: user.role,
})

const MenuTop = ({
  dispatch,
  menuData = [],
  location: { pathname },
  isGlobalMenuOpen,
  // role,
}) => {
  const [, /* selectedKeys */ setSelectedKeys] = useState(store.get('app.menu.selectedKeys') || [])

  useEffect(() => {
    applySelectedKeys()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pathname, menuData])

  const applySelectedKeys = () => {
    const flattenItems = (items, key) =>
      items.reduce((flattenedItems, item) => {
        flattenedItems.push(item)
        if (Array.isArray(item[key])) {
          return flattenedItems.concat(flattenItems(item[key], key))
        }
        return flattenedItems
      }, [])
    const selectedItem = find(flattenItems(menuData, 'children'), ['url', pathname])
    setSelectedKeys(selectedItem ? [selectedItem.key] : [])
  }

  const toggleGlobalMenu = () => {
    dispatch({
      type: 'settings/CHANGE_SETTING',
      payload: {
        setting: 'isGlobalMenuOpen',
        value: !isGlobalMenuOpen,
      },
    })
  }

  return (
    <div className={classNames(`${style.menu}`, { [style.white]: true }, 'align-items-center')}>
      <div className={style.logoContainer}>
        <div className={style.logo}>
          <img
            src="/resources/images/logo-worksaurus-2.png"
            className="mr-2"
            style={{ height: 30 }}
            alt="Logo"
          />
        </div>
      </div>
      <div className={style.navigation}>
        <Alert
          className="d-none d-sm-block"
          icon={<SoundOutlined />}
          message="Please confirm your email in the next 14 days. Lorem ipsum dolor sit amet, consectetur adipiscing elit."
          type="warning"
          showIcon
          closable
        />
      </div>
      <div className="d-flex p-2 mr-4 align-items-center">
        {/* <div className="mr-4 d-none d-sm-block">
          <LanguageSwitcher />
        </div> */}
        <div className="mr-4">
          <Button type="primary" size="large" icon={<MenuOutlined />} onClick={toggleGlobalMenu}>
            Global Menu
          </Button>
        </div>
        <div data-classname="mr-4 d-none d-sm-block" className="d-none d-sm-block">
          <Actions />
        </div>
        <div className="">
          <UserMenu />
        </div>
      </div>
    </div>
  )
}

export default withRouter(connect(mapStateToProps)(MenuTop))
