import React from 'react'
import { connect } from 'react-redux'
import { Layout } from 'antd'
import { withRouter } from 'react-router-dom'
import classNames from 'classnames'
import style from './style.module.scss'

const mapStateToProps = ({ settings }) => ({
  logo: settings.logo,
  isGrayTopbar: settings.isGrayTopbar,
  isCardShadow: settings.isCardShadow,
  isSquaredBorders: settings.isSquaredBorders,
  isBorderless: settings.isBorderless,
  authPagesColor: settings.authPagesColor,
})

const AuthLayout = ({ children, isCardShadow, isSquaredBorders, isBorderless, authPagesColor }) => {
  return (
    <Layout>
      <Layout.Content>
        <div className="row">
          <div className="col-md-7 d-none d-md-block">
            <div
              className={classNames(`${style.container}`, {
                'justify-content-end': true,
                'align-items-start': true,
                cui__layout__squaredBorders: isSquaredBorders,
                cui__layout__cardsShadow: isCardShadow,
                cui__layout__borderless: isBorderless,
                [style.white]: authPagesColor === 'white',
                [style.gray]: authPagesColor === 'gray',
              })}
              style={{
                backgroundImage:
                  authPagesColor === 'image' ? 'url(/resources/images/login-background-2.jpg)' : '',
              }}
            >
              <span className="d-inline-block m-4 text-white">
                Copyright &copy; 2020 All rights reserved
              </span>
            </div>
          </div>
          <div className="col-md-5">
            <div className="d-flex align-items-center justify-content-center vh-100">
              <div className={style.containerInner}>{children}</div>
            </div>
          </div>
        </div>
      </Layout.Content>
    </Layout>
  )
}

export default withRouter(connect(mapStateToProps)(AuthLayout))
