import 'antd/lib/style/index.less' // antd core styles
import './components/kit/vendors/antd/themes/default.less' // default theme antd components
import './components/kit/vendors/antd/themes/dark.less' // dark theme antd components
import './global.scss' // app & third-party component styles

import React from 'react'
import ReactDOM from 'react-dom'
import store from 'store'
import {
  createHashHistory,
  // createBrowserHistory
} from 'history'
import { createStore, applyMiddleware, compose } from 'redux'
import { persistStore } from 'redux-persist'
import { Provider } from 'react-redux'
import createSagaMiddleware from 'redux-saga'
import { routerMiddleware } from 'connected-react-router'
import { PersistGate } from 'redux-persist/integration/react'
import NProgress from 'nprogress'

import reducers from './redux/reducers'
import sagas from './redux/sagas'
import Localization from './localization'
import Router from './router'
import * as serviceWorker from './serviceWorker'

NProgress.configure({ showSpinner: false })

// middlewares
const history = createHashHistory()
// const history = createBrowserHistory();

const sagaMiddleware = createSagaMiddleware()
const routeMiddleware = routerMiddleware(history)
const middlewares = [sagaMiddleware, routeMiddleware]
const configureStore = () => {
  const appStore = createStore(reducers(history), compose(applyMiddleware(...middlewares)))
  const persistor = persistStore(appStore)
  return { appStore, persistor }
}

const { appStore, persistor } = configureStore()
sagaMiddleware.run(sagas)

const onBeforeLift = () => {
  const userStore = store.get('persist:user')
  const userSession = store.get('user') || {}
  const { rememberMe } = userStore || {}
  if (rememberMe !== 'true' && userSession.authorized) {
    store.remove('user')
  }
}

ReactDOM.render(
  <Provider store={appStore}>
    <PersistGate onBeforeLift={onBeforeLift} loading={null} persistor={persistor}>
      <Localization>
        <Router history={history} />
      </Localization>
    </PersistGate>
  </Provider>,
  document.getElementById('root'),
)

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister()
export { appStore as store, history }
