import actions from './actions'

const initialState = {
  list: [],
  total: 0,
  searchConfig: {},
  loading: false,
}

export default function helpdeskReducer(state = initialState, action) {
  switch (action.type) {
    case actions.SET_STATE:
      return { ...state, ...action.payload }
    case actions.SEARCH_HELPDESK:
      return {
        ...state,
        searchConfig: {
          ...state.searchConfig,
          ...action.payload
        }
      }
    default:
      return state
  }
}
